body {
  animation: none
}

#dashboard {
  animation: fadeInOpacity 0.5s
}

.section {
  background: none
}

.menu-list a {
  color: #3794d2
}

.menu-list a:hover {
  color: #60a8dc;
  background-color: #4d4d4d
}

.menu-list a.is-active {
  color: #eff0f1;
  background-color: #3794d2
}

.menu-list a[disabled] {
  color: #7a7a7a;
  cursor: not-allowed
}

.menu-list a[disabled]:hover {
  background: none
}

ul#albumsContainer {
  border-left: 0;
  padding-left: 0
}

ul#albumsContainer li {
  border-left: 1px solid #898b8d;
  padding-left: 0.75em;
  animation: fadeInOpacity 0.5s
}

#page.fade-in {
  animation: fadeInOpacity 0.5s
}

.pagination {
  margin-bottom: 1.25rem
}

.pagination a:not([disabled]) {
  color: #eff0f1;
  border-color: #4d4d4d;
  background-color: #31363b
}

a.pagination-link:not(.is-current):hover,
a.pagination-next:not([disabled]):hover,
a.pagination-previous:not([disabled]):hover {
  color: #eff0f1;
  border-color: #60a8dc;
  background-color: #31363b
}

a.pagination-link.is-current {
  background-color: #3794d2;
  border-color: #3794d2
}

a.pagination-link.is-current:hover {
  border-color: #60a8dc
}

li[data-action="page-ellipsis"] {
  cursor: pointer
}

.label {
  color: #bdc3c7
}

.menu-list li ul {
  border-left-color: #898b8d
}

.image-container .checkbox {
  position: absolute;
  top: 12px;
  left: 12px
}

.no-touch .image-container .checkbox {
  opacity: 0.5
}

.no-touch .image-container .controls,
.no-touch .image-container .details {
  opacity: 0
}

.no-touch .image-container:hover .checkbox,
.no-touch .image-container:hover .controls,
.no-touch .image-container:hover .details {
  opacity: 1
}

#page {
  /* fix overflow issue with flex */
  min-width: 0
}

.table {
  color: #bdc3c7;
  background-color: #31363b;
  font-size: 0.75rem
}

.table tr:hover,
.table.is-striped tbody tr:nth-child(2n) {
  background: none
}

.table.is-striped tbody tr:hover,
.table.is-striped tbody tr:nth-child(2n):hover,
.tag {
  background-color: #4d4d4d
}

.table td,
.table th {
  border: 0;
  white-space: nowrap
}

.table th {
  color: #eff0f1;
  height: 2.25em
}

.table thead td,
.table thead th {
  color: #eff0f1;
  background-color: #ff3860
}

.table .cell-indent {
  padding-left: 2.25em
}

.is-linethrough {
  text-decoration: line-through
}

#menu.is-loading li a {
  cursor: progress
}

#statistics tr *:nth-child(2) {
  min-width: 50%
}

.expirydate {
  color: #bdc3c7
}
